import "core-js/modules/es.array.push.js";
import { onMounted, reactive, toRefs, computed } from 'vue';
import FoodListItem from '../../../components/FoodListItem.vue';
import { useStore } from 'vuex';
import { showToast } from 'vant';
import { useRouter } from 'vue-router';
export default {
  components: {
    FoodListItem
  },
  props: ['changeShow'],
  setup(props) {
    const data = reactive({
      checked: [],
      //item选中的数量
      submitChecked: true //全选按钮
    });

    const store = useStore();
    const router = useRouter();

    // 处理步进器的值与数据同步
    const handleChange = (value, detail) => {
      store.state.cartList.map(item => {
        if (item.id === detail.name) {
          item.num = value;
        }
      });
    };

    // 处理购物车里面商品选中
    const init = () => {
      data.checked = store.state.cartList.map(item => item.id);
    };

    // 全选的逻辑 ****
    const choseAll = () => {
      // 如果item选中的数量不等于 store存在选中的数量，则把store中的数量赋给data中的chcked
      // 全部选中，或者全不选
      if (data.checked.length !== store.state.cartList.length) {
        init();
      } else {
        data.checked = [];
      }
    };
    // 没有全选的情况下，则全选按钮为false，否则为true
    const groupChange = result => {
      console.log(result);
      if (result.length === store.state.cartList.length) {
        data.submitChecked = true;
      } else {
        data.submitChecked = false;
      }
      data.checked = result;
    };
    //           ****

    // 计算属性，计算选中的商品价格
    const allPrice = computed(() => {
      // 筛选出被选中的元素
      let countList = updateData();
      let num = 0;
      countList.forEach(item => {
        num += item.num * item.price;
      });
      return num * 100;
    });
    const updateData = type => {
      return store.state.cartList.filter(item => {
        // 判断 carList 数组里面是否包含 checked数组里的元素
        return type === 'delete' ? !data.checked.includes(item.id) : data.checked.includes(item.id);
      });
    };

    // 删除按钮的逻辑
    const handleDelete = () => {
      if (data.checked.length) {
        let countList = updateData('delete');
        store.commit('delete', countList);

        // 当商品被删完时，回复到空列表页面
        data.checked = [];
        if (!store.state.cartList.length) {
          props.changeShow();
          store.commit('edit');
        }
      } else {
        showToast('请选择要删除的商品');
      }
    };

    // 结算的逻辑
    const onSubmit = () => {
      if (data.checked.length) {
        store.commit('pay', updateData());
        router.push({
          path: '/createOrder',
          query: {
            // 跳转的时候，把选中的商品信息带过去订单页面
            list: data.checked
          }
        });
      } else {
        showToast('请选择要结算的商品');
      }
    };
    onMounted(() => {
      init();
    });
    return {
      ...toRefs(data),
      store,
      handleChange,
      choseAll,
      groupChange,
      allPrice,
      onSubmit,
      handleDelete
    };
  }
};